import axios from 'axios';

// 创建视频
const postVideo = form => axios.post('/api/v1/videos', form).then(res => res.data);

// 读视频详情
const getVideo = id => axios.get(`/api/v1/video/${id}`).then(res => res.data);

// 读取视频列表
const getPeopleList = () => axios.get('/api/v1/people').then(res => res.data);

const getSlide = () => axios.get('/api/v1/slide').then(res => res.data);

const getDomain = () => axios.get('/api/v1/domain').then(res => res.data);

const getDocument = () => axios.get('/api/v1/doc').then(res => res.data);

const getNews = () => axios.get('/api/v1/news').then(res => res.data);

const getPublication = () => axios.get('/api/v1/publication').then(res => res.data);

const getActivity = () => axios.get('/api/v1/activity').then(res => res.data);

export {
  getPeopleList,
  getVideo,
  getSlide,
  getDomain,
  getDocument,
  getNews,
  getPublication,
  getActivity,
  postVideo,
};
