import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/index.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/research',
      name: 'research',
      component: () => import('./views/research.vue'),
    },
    {
      path: '/people',
      name: 'people',
      component: () => import('./views/people.vue'),
    },
    {
      path: '/publication',
      name: 'publication',
      component: () => import('./views/publication.vue'),
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import('./views/contact.vue'),
    },
    {
      path: '/newslist',
      name: 'newslist',
      component: () => import('./views/newslist.vue'),
    },
    {
      path: '/protocol',
      name: 'protocol',
      component: () => import('./views/protocolonlinedata.vue'),
    },
    // {
    //   path: '/pdf/:id',
    //   name: 'pdf',
    //   component: () => import('./views/PDFviewer.vue'),
    //   props: true,
    // },
    {
      path: '/life',
      name: 'life',
      component: () => import('./views/lifelist.vue'),
    },
  ],
});
