<template>
	<div>
		<div class="restrict banner">
			<div class="pic">
				<ul class="clearfix">
					<li class="zpic" v-for="i in images" :key="i.src">
						<img :src="i.src">
						<div class="txt">
							<p class="p1">{{ i.title }}</p>
						</div>
					</li>
				</ul>
				<div class="progress">
					<div class="piece"></div>
				</div>
			</div>
		</div>
		<div class="restrict intr">
			<div class="row intrtop clearfix">
				<div class="ResearchArea left">
					<div class="Title">
						RESEARCH AREA
					</div>
					<div class="MoreBtn">
						<router-link to="/research">More</router-link>
					</div>
					<ul class="list">					
						<li v-for="i in areas" :key="i"><a href="javascript:void(0);">{{i}}</a></li>
					</ul>
				</div>
				<div class="News right">
					<div class="Title">
						NEWS
					</div>
					<div class="MoreBtn">
						<router-link to="/newslist">More</router-link>
					</div>
					<ul class="list">
						<li v-for="(i, j) in news" :key="j">
							<a href="javascript:void(0);">
								<span>{{ i.time }}</span>
								<p>{{ i.title }}</p>
							</a>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { getSlide, getDomain, getNews } from '@/api/video/index.js'
export default {
	data: () => ({
		images:[],
		areas:[],
		news:[]
	}),
	methods: {
		load: function() {
			getSlide().then( res => {
				this.images = res.data.map(function(v){
					return {
						title: v.title, 
						src: v.imgpath,
					}
				})
			}),
			getDomain().then( res => {
				var areas = res.data.map(v => v.econtents)
				this.areas = areas
				this.$store.commit('newArea', areas)
			}),
			getNews().then( res => {
				var newsList = res.data.map(v => ({
					time:v.stime.substring(0, 10),
					title:v.title
				}))
				this.news = newsList.slice(0, 2)
				this.$store.commit('addNews', newsList)
			})
		},
	},
	beforeMount() {
		this.load()
	}
};
</script>