import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    areas:[],
    news:[]
  },
  mutations: {
    newArea(state, area) {
      state.areas = area
    },
    addNews(state, news) {
      state.news = news
    },
  },
  actions: {

  },
});
